import {Component, Input} from '@angular/core';


@Component({
  selector: 'loading',
  templateUrl: 'loading.html'
})
export class LoadingComponent {

  private _text: string;


  @Input()
  set text(text:string){
    this._text = text;
  }
  get text():string{
    return this._text;
  }

  constructor() {}

}



