import { Component, ElementRef, ViewChild,Input } from '@angular/core';
import { ActionSheetController} from 'ionic-angular';
import { HttpService }            from '../../service/httpService';
declare var echarts;


@Component({
  selector: 'statistic',
  templateUrl: 'statistic.html'
})
export class StatisticComponent {

  private options = {
    title: {
      subtext: '重庆时时彩'
    },
    color: ['#ff153c'],
    tooltip : {
      show:true,
      trigger: 'axis',
      axisPointer : {            // 坐标轴指示器，坐标轴触发有效
        type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
      }
    },
    legend: {
      data:['次数']
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
    },
    xAxis : [
      {
        type : 'category',
        data : [],
        axisTick: {
          alignWithLabel: true
        }
      }
    ],
    yAxis : [
      {
        type : 'value'
      }
    ],
    toolbox: {
      show: true,
      feature: {
        // dataView: {readOnly: false},
        magicType: {type: ['line', 'bar']},
      }
    },
    series : [
      {
        name:'次数',
        type:'bar',
        barWidth: '60%',
        data:[],
        smooth:true,
        itemStyle: {
          normal: {
            label: {
              show: true, //开启显示
              position: 'top', //在上方显示
              textStyle: { //数值样式
                color: 'black',
                fontSize: 12
              }
            }
          }
        }
      }
    ]
  };
  private sortDef = true;
  @ViewChild('chart') container: ElementRef;
  chart: any;
  private lottery:any;
  private number = 30;
  private numberStr = "近30期";
  private pointStr = "定位冷热";

  private statisticalCode:string;
  private tapUnit = [
    {code:'1',name:'万',select:true},
    {code:'2',name:'千',select:true},
    {code:'3',name:'百',select:true},
    {code:'4',name:'十',select:true},
    {code:'5',name:'个',select:true},
  ];

  @Input()
  set setLottery(data){
    this.lottery = data;
    this.options.title.subtext = data.title
    if(data.type == 'ssc'){
      this.statisticalCode = 'sscdwlr';
    }else if(data.type == 'pk10'){
      this.statisticalCode = 'pk10dwlr';
    }else if(data.type == '11x5'){
      this.statisticalCode = '11x5dwlr'
    }else if(data.type == 'k3'){
      this.statisticalCode = "k3dwlr";
    }
    for(var i=0;i<this.tapUnit.length;i++){
      this.tapUnit[i].select = true;
    }
    this.number = 30;
    this.numberStr = "近30期";

    this.getData();
  }

  constructor(public http:HttpService,
              public actionSheet:ActionSheetController) {

  }

  ngAfterViewInit(){


    let ctx = this.container.nativeElement;
    this.chart = echarts.init(ctx);
  }
  
  
  getData(){
    
    
    let url = "/api/lotteryColdHot/getStatistics";


    var position = [];

    for(var i=0;i<this.tapUnit.length;i++){
      if(this.tapUnit[i].select){
        position.push(this.tapUnit[i].code);
      }
    }
    let map = {
      parentLotteryCategoryCode: this.lottery.type,
      statisticalCode: this.statisticalCode,
      lotteryCategoryCode: this.lottery.code,
      periods: this.number,
      position: position.join(','),
      sort: this.sortDef ? 0 : 1
    };
    let self = this;
    this.http.post(url,map,function (res,err) {
      if(!err){
        let array = [];
        if(res.length > 0){
          let data = res[0];
          self.options.xAxis[0].data = [];
          self.options.series[0].data = [];
          for(var i=0;i<data.list.length;i++){
            self.options.xAxis[0].data.push(data.list[i].number);
            self.options.series[0].data.push(data.list[i].count);
          }
          self.chart.setOption(self.options);

        }
      }
    })
  }

  selectBarIndex(){

    let actionSheet = this.actionSheet.create({
      buttons: [
        {
          text: '近30期',
          handler: () => {
            this.numberStr = "近30期";
            this.number = 30;
            this.getData();
          }
        },
        {
          text: '近60期',
          handler: () => {
            this.numberStr = "近60期";
            this.number = 60;
            this.getData();
          }
        },
        {
          text: '近90期',
          handler: () => {
            this.numberStr = "近90期";
            this.number = 90;
            this.getData();
          }
        },
        {
          text: '取消',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }
  selectNumberAction(){
    let buttons = [];
    if (this.lottery.type == 'ssc'){
      buttons = [
        {text: '定位冷热', handler: () => {this.statisticalCode = 'sscdwlr';this.pointStr = '定位冷热';this.getData();}},
        {text: '定位大小', handler: () => {this.statisticalCode = 'sscdwdx';this.pointStr = '定位大小';this.getData();}},
        {text: '定位单双', handler: () => {this.statisticalCode = 'sscdwds';this.pointStr = '定位单双';this.getData();}},
        {text: '万个龙虎', handler: () => {this.statisticalCode = 'ssclh';this.pointStr = '万个龙虎';this.getData();}},
        {text: '万个和尾', handler: () => {this.statisticalCode = 'sschw';this.pointStr = '万个和尾';this.getData();}},
        {text: '取消', role: 'cancel', handler: () => {console.log('Cancel clicked');}}]
    }else if(this.lottery.type == 'k3'){
      buttons = [
        {text: '定位冷热', handler: () => {this.statisticalCode = 'k3dwlr';this.pointStr = '定位冷热';this.getData();}},
        {text: '取消', role: 'cancel', handler: () => {console.log('Cancel clicked');}}]
    }else if (this.lottery.type == '11x5'){
      buttons = [
        {text: '定位冷热', handler: () => {this.statisticalCode = '11x5dwlr';this.pointStr = '定位冷热';this.getData();}},
        {text: '取消', role: 'cancel', handler: () => {console.log('Cancel clicked');}}]
    }else if (this.lottery.type == 'pk10'){
      buttons = [
        {text: '定位冷热', handler: () => {this.statisticalCode = 'sscdwlr';this.pointStr = '定位冷热';this.getData();}},
        {text: '定位大小', handler: () => {this.statisticalCode = 'sscdwdx';this.pointStr = '定位大小';this.getData();}},
        {text: '定位单双', handler: () => {this.statisticalCode = 'sscdwds';this.pointStr = '定位单双';this.getData();}},
        {text: '取消', role: 'cancel', handler: () => {console.log('Cancel clicked');}}]
    }else {
      return;
    }
    let actionSheet = this.actionSheet.create({buttons});
    actionSheet.present();
  }

  sortAction(index){
    this.sortDef = index;
    this.getData();
  }

  unitAction(index){
    this.tapUnit[index].select = !this.tapUnit[index].select;
    this.getData();
  }

}




















