import {Component, Input, Output} from '@angular/core';
import { ActionSheetController} from 'ionic-angular';
import { HttpService }            from '../../service/httpService';


@Component({
  selector: 'k3',
  templateUrl: 'k3.html'
})
export class K3Component {

  private lotteryCode:string;
  private indexBarStr:string;      //当前第几球
  private indexBar:number = 0;     //第几球索引
  private navBar = [];             //时时彩导航
  private navBarIndex = 0;         //时时彩导航索引
  private number = 30;
  private numberStr = "近30期";
  @Input()
  set setLotteryCode(code:string){
    this.lotteryCode = code;
    this.getData();
  }
  constructor(public actionSheet:ActionSheetController,
              public http:HttpService) {
    this.indexBarStr = "第一球";
    this.navBar = [
      {
        select:true,
        type:0,
        name:'基本走势',
        item:[],
        url:'/api/lotteryOpenNumber/getLotteryBaseTrend',
        dataSource:[],
        currentData:[]
      },{
        select:false,
        type:1,
        name:'大小走势',
        subIndex:0,
        item:[{
          select:true,
          subType:0,
          name:'大小个数',
        }, {
          select:false,
          subType:0,
          name:'大小比',
        }
        ],
        url:'/api/lotteryOpenNumber/getBigSmallTrendAboutKs',
        dataSource:[],
        currentData:[]
      },{
        select:false,
        type:2,
        name:'定位走势',
        subIndex:0,
        item:[],
        url:'/api/lotteryOpenNumber/getElevnFiveOrFastThreePositionTrend',
        dataSource:[],
        currentData:[]
      },{
        select:false,
        type:3,
        name:'奇偶走势',
        subIndex:0,
        item:[{
          select:true,
          subType:0,
          name:'奇偶个数',
        }, {
          select:false,
          subType:0,
          name:'奇偶比',
        }],
        url:'/api/lotteryOpenNumber/getOddEvenTrendAboutKs',
        dataSource:[],
        currnetData:[]
      },{
        select:false,
        type:4,
        name:'和值走势',
        subIndex:0,
        item:[{
          select:true,
          subType:0,
          name:'和值走势',
        }, {
          select:false,
          subType:0,
          name:'和值形态',
        }],
        url:'/api/lotteryOpenNumber/getLotterySumTrend',
        dataSource:[],
        currentData:[]
      }
    ];
  }
  ngAfterViewChecked(){
    if(this.navBarIndex == 2){
      let svg = document.getElementById('svg');
      let node = document.getElementsByClassName('zhong');
      svg.innerHTML = this.createLine(node,'#00baff');
    }if(this.navBarIndex == 4){
      let svg = document.getElementById('svg1');
      let node = document.getElementsByClassName('zhong2');
      svg.innerHTML = this.createLine(node,'#00baff');
    }
  }

  createLine(bars,lineColor){
    let svgStr = "";
    if(bars.length <= 0)return;
    for(var i=0;i<this.number - 1;i++){
      let td = bars[i].parentNode;
      let top = td.offsetTop;
      let left = td.offsetLeft;
      let width = td.offsetWidth / 2;
      let height = td.offsetHeight / 2;
      let tdX = left + width;
      let tdY = top + height;
      let td1 = bars[i+1].parentNode;
      let top1 = td1.offsetTop;
      let left1 = td1.offsetLeft;
      let width1 = td1.offsetWidth / 2;
      let height1 = td1.offsetHeight / 2;
      let td1X = left1 + width1;
      let td1Y = top1 + height1;
      svgStr += "<line style='stroke:"+ lineColor + "' x1='"+tdX+"' y1='"+tdY+"' x2='"+td1X+"' y2='"+td1Y+"'/>";
    }
    return svgStr;
  }

  getData(){

    let url = this.navBar[this.navBarIndex].url;
    let self = this;
    let navBarIndex = self.navBarIndex
    this.http.post(url,{lotteryCategoryCode:this.lotteryCode,number:this.number},function (data,err) {
      if(!err){
        if(navBarIndex == 0){
          data[0].trendList = data[0].trendList.reverse();
          for(var j=0;j<data[0].trendList.length;j++){
            let item = data[0].trendList[j];
            item.openNumber = item.openNumber.split(',');
            item.preIssue = item.preIssue.substring(item.preIssue.length - 4,item.preIssue.length);
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data[self.indexBar].trendList;
        }else if(navBarIndex == 1){

          data[0].bigSmallList = data[0].bigSmallList.reverse();
          data[0].bigSmallRatioList = data[0].bigSmallRatioList.reverse();
          for(var i=0;i<data[0].bigSmallList.length;i++){
            let item = data[0].bigSmallList[i];
            item.openNumber = item.openNumber.split(',');
            item.preIssue = item.preIssue.substring(item.preIssue.length - 4,item.preIssue.length);
            let item1 = data[0].bigSmallRatioList[i];
            item1.openNumber = item1.openNumber.split(',');
            item1.preIssue = item1.preIssue.substring(item.preIssue.length - 4,item.preIssue.length);
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data[0].bigSmallList;
        }else if(navBarIndex == 2){

          for(var i=0;i<data.length;i++){
            var group = data[i];
            group.trendList = group.trendList.reverse();
            for(var j=0;j<group.trendList.length;j++){
              var item = group.trendList[j];
              item.openNumber = item.openNumber.split(',');
              item.preIssue = item.preIssue.substring(item.preIssue.length - 4,item.preIssue.length);
            }
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data[self.indexBar].trendList;
        }else if(navBarIndex == 3){
          data[0].oddEvenList = data[0].oddEvenList.reverse();
          data[0].oddEvenRatioArray = data[0].oddEvenRatioArray.reverse();
          for(var i=0;i<data[0].oddEvenList.length;i++){
            let item = data[0].oddEvenList[i];
            item.openNumber = item.openNumber.split(',');
            item.preIssue = item.preIssue.substring(item.preIssue.length - 4,item.preIssue.length);
            let item1 = data[0].oddEvenRatioArray[i];
            item1.openNumber = item1.openNumber.split(',');
            item1.preIssue = item1.preIssue.substring(item.preIssue.length - 4,item.preIssue.length);
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data[0].oddEvenList;
        }else if(navBarIndex == 4){

          data.trendList = data.trendList.reverse();

          for(var i=0;i<data.trendList.length;i++){
            let item = data.trendList[i];
            item.openNumber = item.openNumber.split(',');
            item.preIssue = item.preIssue.substring(item.preIssue.length - 4,item.preIssue.length);
            item.with = 0;
            for(var j=0;j<item.openNumber.length;j++){
              var number = parseInt(item.openNumber[j]);
              item.with += number;
            }
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data.trendList;
        }
      }
    })
  }

  onSelectItem(index){
    if(this.navBarIndex == index) return;
    this.navBarIndex = index;
    this.indexBar = 0;
    this.indexBarStr = "第一球";
    this.getData();
  }
  selectSubBarAction(index){
    for(var i=0;i<this.navBar[this.navBarIndex].item.length;i++){
      this.navBar[this.navBarIndex].item[i].select = false;
    }
    this.navBar[this.navBarIndex].item[index].select = true;
    this.navBar[this.navBarIndex].subIndex = index;
    if(this.navBarIndex == 1){
      if(index == 0){
        this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[0].bigSmallList
      }else if(index == 1){
        this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[0].bigSmallRatioList
      }
    }else if(this.navBarIndex == 3){
      if(index == 0){
        this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[0].oddEvenList
      }else if(index == 1){
        this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[0].oddEvenRatioArray
      }
    }
  }

  //当前第几球
  selectBarIndex(){
    let actionSheet = this.actionSheet.create({
      buttons: [
        {
          text: '第一球',
          handler: () => {
            this.indexBarStr = "第一球";
            this.indexBar = 0;
            this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[0].trendList;
          }
        },
        {
          text: '第二球',
          handler: () => {
            this.indexBarStr = "第二球";
            this.indexBar = 1;
            this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[1].trendList;
          }
        },
        {
          text: '第三球',
          handler: () => {
            this.indexBarStr = "第三球";
            this.indexBar = 2;
            this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[2].trendList;
          }
        },
        {
          text: '取消',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  selectNumberAction(){

    let actionSheet = this.actionSheet.create({
      buttons: [
        {
          text: '近30期',
          handler: () => {
            this.numberStr = "近30期";
            this.number = 30;
            this.getData();
          }
        },
        {
          text: '近60期',
          handler: () => {
            this.numberStr = "近60期";
            this.number = 60;
            this.getData();
          }
        },
        {
          text: '近90期',
          handler: () => {
            this.numberStr = "近90期";
            this.number = 90;
            this.getData();
          }
        },
        {
          text: '取消',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }
}



