import { Component } from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import { HttpService } from '../../../service/httpService';
import { BrowserTab } from '@ionic-native/browser-tab';
@Component({
  selector: 'page-find',
  templateUrl: 'find.html',
})
export class FindPage {


  private data:any;
  private showLoading = true;

  constructor(public navCtrl: NavController,
              public http:HttpService,
              public browser:BrowserTab,
              public navParams: NavParams) {
  }

  ionViewDidLoad() {

    let url = "/api/applicationDownload/getList";
    let self = this;
    this.http.post(url,{},function (res,err) {
      self.showLoading = false;
      if (!err){
        self.data = res;
      }
    });
  }

  downloadAction(index){
    this.browser.isAvailable().then( activatable =>{
      if(activatable){
        this.browser.openUrl(this.data[index].downloaUrl);
      }
    });
  }
}



