import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import {PlanDetailsPage} from "../../plan-details/plan-details";
import {ArticlesDetailsPage} from "../../articles-details/articles-details";

@Component({
  selector: 'page-plan',
  templateUrl: 'plan.html',
})
export class PlanPage {

  constructor(public navCtrl: NavController) {


  }

  articlesDetailsAction(e){
    this.navCtrl.push(ArticlesDetailsPage,{id:e.id});
  }

  planDetailsAction(e){
    this.navCtrl.push(PlanDetailsPage,{data:e});
  }

}



