import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Clipboard }              from '@ionic-native/clipboard';
import { NativeService } from '../../../service/NativeService';
import {HttpService} from "../../../service/httpService";
import {UserService} from "../../../service/userService";
import { LoginPage } from '../../account/login/login';

@Component({
  selector: 'page-details',
  templateUrl: 'details.html',
})
export class DetailsPage {

  private data:any;
  private showLoading = false;
  constructor(public navCtrl: NavController,
              public clip:Clipboard,
              public http:HttpService,
              public user:UserService,
              public nat:NativeService,
              public navParams: NavParams) {

    let obj = this.navParams.get("data");
    let self = this;
    if(obj.forcastResults){
      this.setData(obj);
    }else {
      this.showLoading = true;
      let url = "/api/plan/getPlanPlay";
      this.http.post(url,{zqdmId:obj.zqdmId,planId:obj.planId},function (res,err) {
        self.showLoading = false;
        if(!err){
          self.setData(res);
        }
      });
    }
  }

  setData(obj){
    for(var i=0;i<obj.forcastResults.length;i++){
      let o = obj.forcastResults[i];
      o.hitPeriod = o.hitPeriod.substring(o.hitPeriod.length - 3,o.hitPeriod.length);
      o.end = o.endPeriod.substring(o.endPeriod.length - 3,o.endPeriod.length);
      o.start = o.startPeriod.substring(o.startPeriod.length - 3,o.startPeriod.length);
    }
    this.data = obj;
  }

  shouchangAction() {

    if (!this.user.login){
      this.navCtrl.push(LoginPage);
      return;
    }

    let url = "/api/newforcastresult/addMyPlan";
    let map = {
      loginToken:this.user.loginToken,
      uid:this.user.uid,
      planId:this.data.id
    };
    let self = this;
    this.http.post(url,map,function (res,err) {

      if(!err){
        self.nat.showToast("收藏成功");
      }
    });

  }

  copyAction(){
    this.clip.copy(this.data.forcastResults[0].forcastNumber).then( res =>{
      this.nat.showToast("复制成功");
    });
  }
}



