import {Component, Input, Output} from '@angular/core';
import { ActionSheetController} from 'ionic-angular';
import { HttpService }            from '../../service/httpService';
@Component({
  selector: 'ssc',
  templateUrl: 'ssc.html'
})

export class SscComponent {

  private lotteryCode:string;
  private indexBarStr:string;      //当前第几球
  private indexBar:number = 0;     //第几球索引
  private navBar = [];             //时时彩导航
  private navBarIndex = 0;         //时时彩导航索引
  private number = 30;
  private numberStr = "近30期";

  @Input()
  set setLotteryCode(code:string){
    this.lotteryCode = code;
    this.getData();
  }


  constructor(public actionSheet:ActionSheetController,
              public http:HttpService) {
    this.indexBarStr = "第一球";
    this.navBar = [
      {
        select:true,
        type:0,
        name:'基本走势',
        item:[],
        url:'/api/lotteryOpenNumber/getLotteryBaseTrend',
        dataSource:[],
        currentData:[]
      },{
        select:false,
        type:1,
        name:'定位走势',
        subIndex:0,
        item:[{
          select:true,
          name:'形态特征',
        }, {
          select:false,
          name:'012路',
        }, {
          select:false,
          name:'升平降',
        }
        ],
        url:'/api/lotteryOpenNumber/getLotteryPositionTrend',
        dataSource:[],
        currentData:[]
      },{
        select:false,
        type:2,
        name:'形态走势',
        subIndex:0,
        item:[
          {
            select:true,
            name:'前三形态',
          }, {
            select:false,
            name:'中三形态',
          }, {
            select:false,
            name:'后三形态',
          }],
        url:'/api/lotteryOpenNumber/getLotteryFormTrend',
        dataSource:[],
        currentData:[]
      },{
        select:false,
        type:3,
        name:'冷热分析',
        item:[],
        url:'/api/lotteryOpenNumber/getColdAndHotAnalysis',
        dataSource:[],
        currnetData:[]
      },{
        select:false,
        type:4,
        name:'龙虎走势',
        item:[],
        url:'/api/lotteryOpenNumber/getLotteryDragonTigerTrend',
        dataSource:[],
        currentData:[]
      },
    ]

  }
  ngAfterViewChecked(){

    if(this.navBarIndex == 0){
      let svg = document.getElementById('svg');
      let node = document.getElementsByClassName('zhong');
      svg.innerHTML = this.createLine(node,'#00baff');
    }else if(this.navBarIndex == 2){

      if(this.navBar[this.navBarIndex].subIndex == 0){
        let svg1 = document.getElementById('svg1');
        let svg = document.getElementById('svg2');
        let node = document.getElementsByClassName('zhong0');
        let node1 = document.getElementsByClassName('orange0');
        svg.innerHTML = this.createLine(node1,'orange');
        svg1.innerHTML = this.createLine(node,'#00baff');
      }else if(this.navBar[this.navBarIndex].subIndex == 1){
        let svg1 = document.getElementById('svg3');
        let svg = document.getElementById('svg4');
        let node = document.getElementsByClassName('zhong1');
        let node1 = document.getElementsByClassName('orange1');
        let html =  this.createLine(node1,'orange');
        svg.innerHTML = html;
        svg1.innerHTML = this.createLine(node,'#00baff');
      }else if(this.navBar[this.navBarIndex].subIndex == 2){
        let svg1 = document.getElementById('svg5');
        let svg = document.getElementById('svg6');
        let node = document.getElementsByClassName('zhong');
        let node1 = document.getElementsByClassName('orange');
        let html =  this.createLine(node1,'orange');
        svg.innerHTML = html;
        svg1.innerHTML = this.createLine(node,'#00baff');
      }
    }else if(this.navBarIndex == 4){
      let svg = document.getElementById('svg7');
      let node = document.getElementsByClassName('zhong');
      svg.innerHTML = this.createLine(node,'#00baff');
    }
  }

  createLine(bars,lineColor){
    let svgStr = "";
    if(bars.length <= 0)return;
    for(var i=0;i<this.number - 1;i++){
      let td = bars[i].parentNode;
      let top = td.offsetTop;
      let left = td.offsetLeft;
      let width = td.offsetWidth / 2;
      let height = td.offsetHeight / 2;
      let tdX = left + width;
      let tdY = top + height;
      let td1 = bars[i+1].parentNode;
      let top1 = td1.offsetTop;
      let left1 = td1.offsetLeft;
      let width1 = td1.offsetWidth / 2;
      let height1 = td1.offsetHeight / 2;
      let td1X = left1 + width1;
      let td1Y = top1 + height1;
      svgStr += "<line style='stroke:"+ lineColor + "' x1='"+tdX+"' y1='"+tdY+"' x2='"+td1X+"' y2='"+td1Y+"'/>";
    }
    return svgStr;
  }

  getData(){

    let url = this.navBar[this.navBarIndex].url;
    let self = this;
    let navBarIndex = self.navBarIndex;
    this.http.post(url,{lotteryCategoryCode:this.lotteryCode,number:this.number},function (data,err) {
      if(!err){
        if(navBarIndex == 0 || navBarIndex == 1){
          for(var i = 0;i<data.length;i++){
            data[i].trendList = data[i].trendList.reverse();
            for(var j=0;j<data[i].trendList.length;j++){
              let item = data[i].trendList[j];
              item.openNumber = item.openNumber.split(',');
              item.preIssue = item.preIssue.substring(4,item.preIssue.length);
            }
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data[self.indexBar].trendList;
        }else if(navBarIndex == 2){
          self.navBar[navBarIndex].dataSource = data;
          data.trendList.reverse();
          for(var i=0;i<data.trendList.length;i++){
            let item = data.trendList[i];
            item.openNumber = item.openNumber.split(',');
            item.preIssue = item.preIssue.substring(4,item.preIssue.length);
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data.trendList;
        }else if(navBarIndex == 3){

          for(var i=0;i<data.length;i++) {
            let group = data[i];
            if (group.rank == 1) {
              group.ranking = "冠军";
            } else if (group.rank == 2) {
              group.ranking = "亚军";
            } else if (group.rank == 3) {
              group.ranking = "季军";
            } else if (group.rank == 4) {
              group.ranking = "第四名";
            } else if (group.rank == 5) {
              group.ranking = "第五名";
            }
            for (var j = 0; j < group.list.length; j++) {
              let item = group.list[j];
              if (item.state == 1) {
                item.name = "热号";
              } else if (item.state == 2) {
                item.name = "温号";
              } else if (item.state == 3) {
                item.name = "冷号";
              }
            }
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data;
        }else if(navBarIndex == 4){
          self.navBar[navBarIndex].dataSource = data;
          data.trendList.reverse();
          for(var i=0;i<data.trendList.length;i++){
            let item = data.trendList[i];
            item.openNumber = item.openNumber.split(',');
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data.trendList;
        }
      }
    })
  }

  onSelectItem(index){
    if(this.navBarIndex == index) return;
    this.navBarIndex = index;
    this.indexBar = 0;
    this.indexBarStr = "第一球";
    this.getData();
  }

  selectSubBarAction(index){
    for(var i=0;i<this.navBar[this.navBarIndex].item.length;i++){
      this.navBar[this.navBarIndex].item[i].select = false;
    }
    this.navBar[this.navBarIndex].item[index].select = true;
    this.navBar[this.navBarIndex].subIndex = index;
    if(this.navBarIndex == 1){
      this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[index].trendList;
    }
  }

  //当前第几球
  selectBarIndex(){
    let actionSheet = this.actionSheet.create({
      buttons: [
        {
          text: '第一球',
          handler: () => {
            this.indexBarStr = "第一球";
            this.indexBar = 0;
            this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[0].trendList;
          }
        },
        {
          text: '第二球',
          handler: () => {
            this.indexBarStr = "第二球";
            this.indexBar = 1;
            this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[1].trendList;
          }
        },
        {
          text: '第三球',
          handler: () => {
            this.indexBarStr = "第三球";
            this.indexBar = 2;
            this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[2].trendList;
          }
        },{
          text: '第四球',
          handler: () => {
            this.indexBarStr = "第四球";
            this.indexBar = 3;
            this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[3].trendList;
          }
        },{
          text: '第五球',
          handler: () => {
            this.indexBarStr = "第五球";
            this.indexBar = 4;
            this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[4].trendList;
          }
        },
        {
          text: '取消',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  selectNumberAction(){

    let actionSheet = this.actionSheet.create({
      buttons: [
        {
          text: '近30期',
          handler: () => {
            this.numberStr = "近30期";
            this.number = 30;
            this.getData();
          }
        },
        {
          text: '近60期',
          handler: () => {
            this.numberStr = "近60期";
            this.number = 60;
            this.getData();
          }
        },
        {
          text: '近90期',
          handler: () => {
            this.numberStr = "近90期";
            this.number = 90;
            this.getData();
          }
        },
        {
          text: '取消',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

}



