import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { RegisterPage } from '../register/register';
import { HttpService } from '../../../service/httpService';
import { NativeService } from '../../../service/NativeService';
import { UserService } from '../../../service/userService';
import { FindPasswordPage } from '../find-password/find-password';
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  private form = {
    stageType:'mk',
    account:'', //13760423729
    password:'', //q123456
  };
  constructor(public navCtrl: NavController,
              public http:HttpService,
              public nat:NativeService,
              public user:UserService,
              public navParams: NavParams) {
  }


  loginAction(){

    if (this.form.account.length <= 0){
      this.nat.showToast("手机号不能为空!");
      return;
    }
    if (!this.nat.checkPhone(this.form.account)){
      this.nat.showToast("手机号格式有误");
      return;
    }

    if(this.form.password.length <= 0){
      this.nat.showToast("密码不能为空");
      return;
    }

    let url = ["otherWay","/api/user/loginCheck"];
    let self = this;
    this.http.post(url,this.form,function (res,err) {
      if(!err){
        self.nat.storageSetValue("userInfo",JSON.stringify(res));
        self.user.setUserInfo(res);
        self.navCtrl.pop();
      }
    });
  }

  registerAction(){
    this.navCtrl.push(RegisterPage);
  }

  findPasswordAction() {
    this.navCtrl.push(FindPasswordPage);
  }

}



