import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HttpService } from '../../../service/httpService';
import { NativeService } from '../../../service/NativeService';
// import {HistoryPage} from "./history";


@Component({
    selector: 'page-lottery',
    templateUrl: 'lottery.html',
})
export class LotteryPage {

    private showLoading=true;
    private navs = [];
    private data:any;
    constructor(public navCtrl: NavController,
                public http:HttpService,
                public nat:NativeService,
                public navParams: NavParams) {


        this.getData();
    }

    getData(){

        let url = "/api/lotteryCategory/getParentLotteryCategory";
        let self = this;
        this.http.post(url,{},function (res,err) {
            if(!err){
                self.navs = [{
                    index:0,
                    name:"全部彩种",
                    parentLotteryCategoryCode: "",
                    parentLotteryCategoryName: "全部彩种",
                    select: true,
                    data:[]
                }
                ];
                for(var i=0;i<res.length;i++){
                    let item = res[i];
                    item.index = i+1;
                    item.select = false;
                    item.data = [];
                    item.name = res[i].parentLotteryCategoryName;
                    self.navs.push(item);
                }
                self.getLotteryOpenNumber('');
            }
        });
    }

    getLotteryOpenNumber(code){

        let url = "/api/lotteryOpenNumber/getDifferentLotteryCategoryNewestOpenNumber";
        let map = {
            parentLotteryCategoryCode:code,
            number:30
        };
        let self = this;
        self.showLoading = true;
        this.http.post(url,map,function (res,err) {
            self.showLoading = false;
            if(!err){
                for(var i=0;i<res.length;i++){
                    res[i].numbers = res[i].lotteryOpenNumber.split(',');
                    // res[i].lotteryOpenTime = res[i].lotteryOpenTime.replace(/-/g,'/');
                    for(var j=0;j<res[i].numbers.length;j++){
                        res[i].numbers[j] = parseInt(res[i].numbers[j]);
                    }
                }
                self.data = res;
                for(var i=0;i<self.navs.length;i++){
                    if(code == self.navs[i].parentLotteryCategoryCode){
                        self.navs[i].data = res;
                    }
                }
            }
        });
    }

    changeNav(index){
        for(var i=0;i<this.navs.length;i++){
            let item = this.navs[i];
            item.select = false;
        }
        this.navs[index].select = true;
        let item = this.navs[index];
        this.data = item.data;
        if(item.data.length > 0){
            return;
        } else {
            this.getLotteryOpenNumber(item.parentLotteryCategoryCode);
        }
    }



    detailsAction(index){

        let obj = this.data[index];

        // this.navCtrl.push(HistoryPage,{data:obj});
    }



















}



