import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import {HttpService} from "../../../service/httpService";
import {UserService} from "../../../service/userService";
import {DetailsPage} from "../../forecast/details/details";


@Component({
  selector: 'page-collection',
  templateUrl: 'collection.html',
})
export class CollectionPage {

  private data = [];
  private showLoading = true;
  constructor(public navCtrl: NavController,
              public http:HttpService,
              public user:UserService,
              public navParams: NavParams) {
  }

  ionViewDidLoad() {


    let url = "/api/newforcastresult/myPlan";
    let map = {uid:this.user.uid,loginToken:this.user.loginToken};
    let self = this;
    this.http.post(url,map,function (res,err) {
      self.showLoading = false;
      if (!err){
        console.log(res);
        self.data = res;
      }
    });
  }

  detailsAction(index){

    this.navCtrl.push(DetailsPage,{data:this.data[index]});
  }

  deletedAction(index){
    console.log(this.data[index]);
    let obj = this.data[index];
    let url = "/api/newforcastresult/removeMyPlan";
    let map = {
      uid:this.user.uid,
      planId:obj.planId,
      loginToken:this.user.loginToken
    };
    let self = this;
    this.http.post(url,map,function (res,err) {
      if(!err){
        console.log(res);
        self.data.splice(index,1);
      }
    });

  }

}



