import { Component} from '@angular/core';
import { NavController, NavParams,ViewController} from 'ionic-angular';

@Component({
  selector: 'page-lotterys',
  templateUrl: 'lotterys.html',
})
export class LotterysPage {
  private lotterys = [];
  private callback:any;
  constructor(public navCtrl: NavController,
              public viewCtrl:ViewController,
              public navParams: NavParams) {
    this.lotterys = navParams.get("lotterys");
    this.callback = navParams.get("callback");
  }


  selectLotteryAction(groupIndex,itemIndex){

    let obj = {};
    for(var i=0;i<this.lotterys.length;i++){
      let group = this.lotterys[i];
      for (var j=0;j<group.list.length;j++){
        let item = group.list[j];
        item.select = false;
        if(groupIndex == i && itemIndex == j){
          item.select = true;
          obj = {
            title:item.lotteryCategoryName,
            code:item.lotteryCategoryCode,
            type:group.parentLotteryCategoryCode
          };
        }
      }
    }
    this.callback(obj);
    this.viewCtrl.dismiss();

  }

  dismiss(){

    this.viewCtrl.dismiss();

  }


}



