import { Component,ComponentFactoryResolver} from '@angular/core';
import {NavController, NavParams, AlertController, ModalController,Events} from 'ionic-angular';
import { HttpService } from '../../service/httpService';
import { NativeService } from '../../service/NativeService';
import { LotterysPage } from '../lotterys/lotterys';

@Component({
  selector: 'page-trend',
  templateUrl: 'trend.html',
})


export class TrendPage {

  private lotterys = [];        //全部彩种
  private type:string;          //当前彩票种类
  private showBaseInfo = false;
  private title:string;         //标题
  private flyIn:string;         //动画控制

  private baseInfo:any;
  private lotteryCode:string;   //时时彩基本数据
  private minutes:number = 0;
  private seconds:number = 0;
  private minutesStr:string = "00";
  private secondsStr:string = "00";
  private dataSource = [];
  private mInterval:number;
  private isOpenNumber = false;

  constructor(public navCtrl: NavController,
              public http:HttpService,
              public nat:NativeService,
              public modal:ModalController,
              public alert:AlertController,
              public component:ComponentFactoryResolver,
              public navParams: NavParams) {
    this.title = "";
    this.getAllLotterys();
  }

  getAllLotterys(){

    let url = "/api/lotteryCategory/getTrendLotteryCategoryList";
    let self = this;
    this.http.post(url,{},function (res,err) {
      if(!err){
        for(var i=0;i<res.length;i++){
          let group = res[i];
          for (var j=0;j<group.list.length;j++){
            let item = group.list[j];
            item.select = false;
            if(i == 0 && j == 0){
              item.select = true;
              self.type = group.parentLotteryCategoryCode;
              self.lotteryCode = item.lotteryCategoryCode;
              self.title = item.lotteryCategoryName;
              self.getLotteryBaseInfo();
            }
          }
        }
        self.lotterys = res;
      }
    })
  }

  getLotteryBaseInfo(){

    let url = "/api/lotteryOpenNumber/getOpenNumberInfomation";
    let self = this;
    this.http.post(url,{lotteryCategoryCode:this.lotteryCode},function (res,err) {
      if(!err){
        self.showBaseInfo = true;
        console.log(res);
        if(self.type != 'qt'){
          res.result = res.analyzeResult.replace(/,/g,' ');
          res.openNumbers = res.lotteryOpenNumber.split(',');
          res.numbers = [];
          for(var i=0; i <res.openNumbers.length;i++){
            res.numbers.push(parseInt(res.openNumbers[i]));
          }
          self.baseInfo = res;
          self.nextOpenTime(res.nextOpenTime.replace(/-/g,'/'));
        }else {
          res.openNumbers = res.lotteryOpenNumber.split(',');
          self.baseInfo = res;
        }
      }
    })
  }

  nextOpenTime(date){
    let openTime = (new Date(date)).getTime() / 1000;
    let currentTime = (new Date()).getTime()/1000;
    if(currentTime < openTime){
      clearInterval(this.mInterval);
      this.mInterval = setInterval( ()=>{
        currentTime = (new Date()).getTime()/1000;
        let time = openTime - currentTime;
        if(time < 0){
          this.isOpenNumber = true;
          clearInterval(this.mInterval);
          this.getLotteryBaseInfo();
        }else {
          this.isOpenNumber = false;
          this.timeInterval(time);
        }
      },1000);
    }else {
      this.isOpenNumber = true;
      this.minutesStr = "00";
      this.secondsStr = "00";
      clearInterval(this.mInterval);
      this.mInterval = setInterval( ()=>{
        this.getLotteryBaseInfo();
      },5000);

    }

  }

  timeInterval(time:number){

    this.minutes = Math.round(time / 60);
    this.seconds = Math.round(time % 60);
    if(this.minutes < 10){
      this.minutesStr = "0" + this.minutes;
    }else {
      this.minutesStr = "" + this.minutes;
    }
    if(this.seconds < 10){
      this.secondsStr = "0" + this.seconds;
    }else {
      this.secondsStr = "" +  this.seconds;
    }
  }


  getData(){

    let url = "/api/lotteryOpenNumber/getNewestOpenNumberByLotteryCategory";
    let self = this;
    this.http.post(url,{lotteryCategoryCode:this.lotteryCode,number:30},function (data,err) {
      if(!err){
        console.log(data);
        for(var i=0;i<data.length;i++){

          data[i].lotteryOpenNumber = data[i].lotteryOpenNumber.split(',');
        }
        self.dataSource = data;
      }
    })
  }

  showModalAction(){
    console.log(this.lotterys)
    let modal = this.modal.create(LotterysPage,{lotterys:this.lotterys,callback:this.callback});
    modal.present();
  }
  callback = (obj)=>{
    return new Promise((res,rej)=>{
      this.title = obj.title;
      this.lotteryCode = obj.code;
      this.type = obj.type;
      this.getLotteryBaseInfo();
      if(obj.type == 'qt'){
        this.getData();
      }
    });
  };

}



