import { NgModule } from '@angular/core';
import { BrowserModule }          from '@angular/platform-browser';
import { LoadingComponent } from './loading/loading';
import { NoticeComponent } from './notice/notice';
import { IonicApp, IonicModule }  from 'ionic-angular';
import { NgxEchartsModule }       from 'ngx-echarts';
import { Pk10Component } from './pk10/pk10';
import { SscComponent } from './ssc/ssc';
import { Select5Component } from './select5/select5';
import { SlowComponent } from './slow/slow';
import { K3Component } from './k3/k3';
import { PipesModule }       from '../pipes/pipes.module';
import { ScrollBarComponent } from './scroll-bar/scroll-bar';
import { PlanComponent } from './plan/plan';
import { StatisticComponent } from './statistic/statistic';
import { PredictionComponent } from './prediction/prediction';
import { TopimgComponent } from './topimg/topimg';

@NgModule({
	declarations: [LoadingComponent,
    NoticeComponent,
    Pk10Component,
    SscComponent,
    Select5Component,
    SlowComponent,
    K3Component,
    ScrollBarComponent,
    PlanComponent,
    StatisticComponent,
    PredictionComponent,
    TopimgComponent],
	imports: [
        PipesModule,
        BrowserModule,
        IonicModule,
        NgxEchartsModule,
    ],
	exports: [LoadingComponent,
    NoticeComponent,
    Pk10Component,
    SscComponent,
    Select5Component,
    SlowComponent,
    K3Component,
    ScrollBarComponent,
    PlanComponent,
    StatisticComponent,
    PredictionComponent,
    TopimgComponent]
})
export class ComponentsModule {}



