import { Component } from '@angular/core';
import { NavController, NavParams,ModalController} from 'ionic-angular';

import { DomSanitizer } from "@angular/platform-browser";
import { HttpService } from '../../service/httpService';
import { NativeService } from '../../service/NativeService';
import { LotterysPage } from '../lotterys/lotterys';
import { DetailsPage } from './details/details'
import {ArticlesDetailsPage} from "../articles-details/articles-details";
import {PlanDetailsPage} from "../plan-details/plan-details";
@Component({
  selector: 'page-forecast',
  templateUrl: 'forecast.html',
})
export class ForecastPage {

  private navs = [];
  private currentIndex = 0;
  private currentLottery = {
    'title':'重庆时时彩',
    'code':'cqssc',
    'type':'ssc'
  };
  constructor(public navCtrl: NavController,
              public dom:DomSanitizer,
              public http:HttpService,
              public nat:NativeService,
              public modal:ModalController,
              public navParams: NavParams) {


    this.navs = [
      {
        url:'/api/lotteryCategory/getForcastLotteryCategoryList',       //彩票列表数据
        name:'中金计划',
        select:true,
        currentLottery:'',//当前彩种
        urls:{
          plans:'/api/newforcastresult/getPlanListByLotteryCategoryCode', //计划列表
          forcast:'/api/newforcastresult/getNewForcast',                  //预测数据
          openNumber:'/api/newforcastresult/getNewOneOpenNumber',         //最新开奖
          nextTime:'/api/newforcastresult/getNextTime'                    //下期开奖时间
        },
        lotterys:[],
        dataSource:[]
      },
      {
        url:'/api/lotteryCategory/getTrendLotteryCategoryList',
        name:'冷热统计',
        select:false,
        currentLottery:'',
        lotterys:[],
        dataSource:[]
      },
      {
        url:'/api/forecastArticles/getForecastLotteryCategory',
        name:'专家计划',
        select:false,
        lotterys:[],
        dataSource:[]
      },
    ];
    this.onSelectItem(0);
    this.getAllLotterys();
  }

  articlesDetailsAction(e){
    this.navCtrl.push(ArticlesDetailsPage,{id:e.id});
  }

  planDetails1Action(e){
    console.log(e);
    this.navCtrl.push(PlanDetailsPage,{data:e});
  }

  onSelectItem(index){
    this.currentIndex = index;
  }

  getAllLotterys(){

    let url = "/api/lotteryCategory/getTrendLotteryCategoryList";
    let self = this;
    this.http.post(url,{},function (res,err) {
      if(!err){
        for(var i=0;i<res.length;i++){
          let group = res[i];
          for (var j=0;j<group.list.length;j++){
            let item = group.list[j];
            item.select = false;
            if(i == 0 && j == 0){
              item.select = true;
              self.navs[1].currentLottery = item.lotteryCategoryName;
            }
          }
        }
        self.navs[1].lotterys = res;
      }
    })
  }

  selectLotteryAction(){

    let modal = this.modal.create(LotterysPage,{lotterys:this.navs[this.currentIndex].lotterys,callback:this.callback})
    modal.present();
  }

  callback = (data)=>{
    return new Promise((res,rej)=>{
      this.navs[this.currentIndex].currentLottery = data.title;
      this.currentLottery = data;
    });
  };

  planDetailsAction(obj){
    this.navCtrl.push(DetailsPage,{data:obj});
  }

}



