import { Component ,ElementRef, ViewChildren, QueryList, ViewChild,
  AfterViewInit} from '@angular/core';
import {NavController, AlertController, Slides} from 'ionic-angular';

import { HttpService } from '../../service/httpService';
import { NativeService } from '../../service/NativeService';

import { NewslistPage } from '../newslist/newslist';
import { ArticlesDetailsPage } from '../articles-details/articles-details';

import { HistoryListPage } from '../historylist/historylist'



@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {


  private banners = [];
  private timeEvent:any = null;
  private noticeWord:string;
  private hotNews = [];
  private y1:number = 0;
  private x1:number = 0;
  private hotLottery = [];
  private newHotDetail = []
  private hotLotteryTimes = [];
  private openTimeInterval;
  private getDataInterval;


  @ViewChildren('noticeText') noticeText:QueryList<ElementRef>;

  ionViewDidLoad(){
    setTimeout(()=>{
      this.getData();
    },1500)
    this.getHotLottery()
  }
  
  timeInterval(){
    if(!this.timeEvent){
      this.timeEvent = setInterval( ()=>{
        this.scrollNumber();
      },50);
    }
  }

  //进入
  ionViewDidEnter() {
    this.timeInterval();
    this.getDetailLottery()
    console.log('enter')

  }
  //离开
  ionViewDidLeave() {
    if(this.timeEvent){
      clearInterval(this.timeEvent);
      this.hotLotteryTimes = []
      this.timeEvent = null;
    }
    clearInterval(this.openTimeInterval)
  }

  scrollNumber(){
    this.y1 -= 1;
    this.x1 -= 1;
    this.noticeText.forEach((ele,index,array)=>{
      let width = 'translateX(-'+(array[array.length -1].nativeElement.clientLeft + array[array.length -1].nativeElement.clientWidth)+'px)';
      ele.nativeElement.style.transform = 'translateX('+this.x1+'px)';
      if(array[array.length -1 ].nativeElement.style.transform == width){
        this.x1 = 0;
        ele.nativeElement.style.transform = 'translateX(0px)';
      }
    });
  }

  getHotLottery(){//获取热点彩票列表
    let self = this
    let url:any = ["otherWay","/openLottery/recommendLottery"];
    this.http.post(url,{},function (res,err) {
      if(!err){
        self.hotLottery = res
        self.getDetailLottery()
      }
    })
  }
  
  getData(){
    let self = this
    let url:any =  ['otherWay','/openLottery/notices']
    this.http.post(url,{},function (res,err) {
      if(!err){
        self.noticeWord=res[0].content
      }
    })

    url = ['otherWay','/openLottery/hotNews']
    this.http.post(url,{},function (res,err) {
      if(!err){
        for(var i=0;i<res.length;i++){
          res[i].newsIntroduce = res[i].newsIntroduce.replace('&nbsp;','')
        }
        self.hotNews = res
      }
    })
  }

  getDetailLottery(){//获取热点彩票详情
    console.log('获取详情')
    let self = this
    let url = '/api/lotteryOpenNumber/getOpenNumberInfomation'
    for(let i=0;i<this.hotLottery.length;i++){
      let params={lotteryCategoryCode:self.hotLottery[i].lotteryCategoryCode}
      self.http.post(url,params,function(res,err){
        if(!err){
          self.getTime(res.nextOpenTime,res.lotteryCategoryCode,res.lotteryPeriods,res.lotteryOpenNumber)
        }
      })
    }
  }
  
  getTime(date,code,periods,nums){
    let self = this
    let openTime = (new Date(date)).getTime() / 1000;
    let currentTime = (new Date()).getTime() / 1000;
    let time;
    if(currentTime<openTime){
       time = Math.floor(openTime-currentTime)
    }
    else{ time = 0}
    let minutes = Math.floor(time/60)
    let seconds = Math.floor(time%60)
    let minutesStr = ('0' + minutes).substr(-2)
    let secondsStr = ('0' + seconds).substr(-2)
    let itemTime = {
      time:time,
      minutes:minutes,
      seconds:seconds,
      minutesStr:minutesStr,
      secondsStr:secondsStr
    }
 
    self.hotLotteryTimes.push(itemTime)
    
    self.hotLottery.forEach(item=>{
      if(item.lotteryCategoryCode==code){
        if(item.lotteryPeriods!=periods){
          item.itemTime=itemTime
          item.lotteryPeriods=periods
          let newNums=[]
          nums.split(',').forEach(element => {
            newNums.push(parseInt(element))
          });
          item.nums=newNums  
        }     
      }
    })
    
    if(self.hotLotteryTimes.length==self.hotLottery.length){ 
      this.newHotDetail = this.hotLottery
      this.openTimeInterval = setInterval(self.timeScroll.bind(this),1000)
      this.getDataInterval = setInterval(self.dataScroll.bind(this),10000)
    }
    
  }
  timeScroll(){//时间每秒刷新
    let self=this
    this.newHotDetail.forEach(item=>{
      //console.log(item.itemTime.time)
      if(item.itemTime.time>0){
        item.itemTime.time--
        item.itemTime.minutes = Math.floor(item.itemTime.time/60)
        item.itemTime.seconds = Math.floor(item.itemTime.time%60)
        item.itemTime.minutesStr = ('0' + item.itemTime.minutes).substr(-2)
        item.itemTime.secondsStr = ('0' + item.itemTime.seconds).substr(-2)
      }else{
        item.itemTime.time = '正在开奖中'
        return false
      } 
    })
  }
  dataScroll(){//10秒检察一次数据
    let self=this
    this.newHotDetail.forEach(item=>{
      if(item.itemTime.time>0){
        return;
      }else{
        self.getOnlyData(item.lotteryCategoryCode)
        return false
      } 
    })
  }
  getOnlyData(code){//请求正在开奖的接口
    let self = this
    let url = '/api/lotteryOpenNumber/getOpenNumberInfomation'
    
    let params={lotteryCategoryCode:code}
    self.http.post(url,params,function(res,err){
      if(!err){
        self.getTime(res.nextOpenTime,res.lotteryCategoryCode,res.lotteryPeriods,res.lotteryOpenNumber)
      }
    })

  }
  gotoNewList(){
    this.navCtrl.push( NewslistPage )
  }
  gotoArticle(id,type){
    console.log(id)
    this.navCtrl.push( ArticlesDetailsPage ,{id:id,type:type})
  }
  gotoHistory(code,parentCode,title){
    this.navCtrl.push( HistoryListPage,{code:code,parentCode:parentCode,title:title})
  }

  constructor(public navCtrl: NavController,
              public alert:AlertController,
              public http:HttpService,
              public nat:NativeService) {
  }

}



