import {Component, EventEmitter, Input, Output,ElementRef,ViewChild,ViewChildren,QueryList} from '@angular/core';

@Component({
  selector: 'scroll-bar',
  templateUrl: 'scroll-bar.html'
})
export class ScrollBarComponent {

  private buttons = [];

  @ViewChildren('ScrollBarItem') barItems:QueryList<ElementRef>;
  @ViewChild('indicator') indicator:ElementRef;
  @Input()
  set setButtonArray(buttons:any){
    this.buttons = buttons;
  }
  @Output() selectItem = new EventEmitter<number>();

  constructor() {

  }

  ngAfterViewInit(){
    let width = window.innerWidth;
    this.barItems.forEach( (ele,index,array)=>{
      var item = ele.nativeElement;
      var lastItem = this.barItems.last.nativeElement;
      var itemMaxX = lastItem.offsetLeft + lastItem.offsetWidth;

      if(itemMaxX < width){
        let itemWidth = (width - 10) / this.barItems.length;

        if(item.offsetWidth < itemWidth){
          item.style.width = itemWidth + 'px';
        }
      }
    });
    let array = this.barItems.toArray();
    for(var i=0;i<this.buttons.length;i++){
      if(this.buttons[i].select == true){
        this.indicator.nativeElement.style.left = array[i].nativeElement.offsetLeft + 'px';
        this.indicator.nativeElement.style.width = array[i].nativeElement.offsetWidth +'px';
      }
    }
  }

  changeNav(index){
    for(var i=0;i<this.buttons.length;i++){
      let item = this.buttons[i];
      item.select = false;
    }
    this.buttons[index].select = true;

    let array = this.barItems.toArray();

    this.indicator.nativeElement.style.left = array[index].nativeElement.offsetLeft + 'px';
    this.indicator.nativeElement.style.width = array[index].nativeElement.offsetWidth +'px';
    this.selectItem.emit(index);
  }

}



