
import { Injectable }     from '@angular/core';
import { HttpClient }     from '@angular/common/http';
import { HTTP }           from '@ionic-native/http';
import { NativeService }  from './NativeService';
import { UserService }    from './userService';
@Injectable()
export class HttpService {
  // public baseUrl:string = "http://192.168.10.79:8100/";
  baseUrl:string = "http://47.244.180.213:8200/";

  public stageType = "mk";
  public channelType = 'mk-qd01';

  constructor(public http: HttpClient,
              public user:UserService,
              public native:NativeService,
              public nativeHttp:HTTP) {

  }

  public post(url: any, params: any = null, callback ) {
    if(url[0]=='otherWay'){
      url = 'http://47.75.180.253:8999' + url[1]
      //url = 'http://192.168.10.79:8999' + url[1]
    }
    else {
      url = this.baseUrl + url
      params.stageType = this.stageType
      params.channelType = this.channelType
    };
    if (this.native.isMobile()){
      this.nativeHttp.post(url,params,{}).then(res=>{
        let obj = JSON.parse(res.data);
        if(obj.status == '200'){
          callback(obj['data'],null);
        }else {
          this.native.showToast(obj["message"]);
          callback(null,obj["message"]);
        }
      }).catch( err =>{
        this.native.hideLoading();
        callback(null,err);
      });
    }else {
      let options = {
        headers:{"Content-Type":"application/json;multipart/form-data"},
        params:params
      };
      this.http.post(url, {}, options).subscribe((res: any) => {
        if(res.status == '200'){
          callback(res['data'],null);
        }else {
          this.native.showToast(res["message"]);
          callback(null,res["message"]);
        }
      }, err => {
        callback(null,err);
      });
    }
  }
  // get数据
  public get(url: string, params: any,callback) {
    params.stageType = this.stageType;
    params.channelType = this.channelType;
    url = this.setGetUrl(url,params);
    if (this.native.isMobile()){
      this.nativeHttp.get(url,{},{}).then(res=>{
        let obj = JSON.parse(res.data);
        if(obj.StatusCode){
          this.native.showToast(obj.Message);
          callback(null,obj.Message);
        }else {
          callback(obj,null);
        }
      }).catch( err =>{
        callback(null,err);
      });
    }else {
      let options = {headers:{}};
      let authtoken = localStorage.getItem('authtoken');
      if (authtoken){
        options.headers = {authtoken:authtoken}
      }
      this.http.get(url,options).subscribe( (res:any) => {
        if(res.StatusCode){
          this.native.showToast(res.Message);
          callback(null,res.Message);
        }else {
          callback(res,null);
        }
      },err=>{
        this.native.hideLoading();
        callback(null,err);
      });
    }
  }
  private setGetUrl(url,parmas:any):string{
    var u = this.baseUrl + url;
    var par = '?';
    for (let key in parmas){
      par += key+"="+parmas[key]+"&";
    }
    u = u + par.substring(0,par.length - 1);
    return u;
  }

}



