import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HttpService } from "../../service/httpService";
import { DomSanitizer} from "@angular/platform-browser";
import { Clipboard }              from '@ionic-native/clipboard';
import {NativeService} from "../../service/NativeService";

@Component({
  selector: 'page-articles-details',
  templateUrl: 'articles-details.html',
})
export class ArticlesDetailsPage {

  private id:any;
  private type:any;
  private data:any={
    newsTitle:'',
    postedTime:'',
    newsContent:''
  };
  constructor(public navCtrl: NavController,
              public http:HttpService,
              public clip:Clipboard,
              public nat:NativeService,
              public domSanitizer:DomSanitizer,
              public navParams: NavParams) {
  }

  ionViewDidLoad(){
    this.getData()
  }
  getData(){
    this.id = this.navParams.get("id");
    this.type = this.navParams.get("type")
    let url = ['otherWay',"/openLottery/articleDetails"];
    let self = this;
    this.http.post(url,{id:this.id},function (res,err) {
      if (!err){
        self.data = res
        console.log(self.data.newsTitle)
      }
    });
  }
  copyAction(){
    this.clip.copy(this.data.nextPeriod).then( res=>{
      this.nat.showToast("复制成功");
    });
  }

}



