import { Pipe, PipeTransform } from '@angular/core';


@Pipe({
  name: 'transnumber',
})
export class TransnumberPipe implements PipeTransform {
  transform(value: any, ...args) {
    let numberChart=['一','二','三','四','五','六','七','八','九','十']
    if(value<=0){
      return "正在开奖"
    }
    else if(value<=10&&value>0){
      return numberChart[value-1] + '分钟开奖'
    }else if(value>10&&value<60){
      let index = value.toString()[0]
      let index1 = value.toString()[1]
      if(index1=='0'){
        return numberChart[index] + '十' + '分钟开奖'
      }else{
        if(index=='1'){
          return '十' +  numberChart[index1] + '分钟开奖'
        }else
        return numberChart[index] + '十' +  numberChart[index1] + '分钟开奖'
      }
    }else if(value>=60&&value<60*24){
      //return value/
    }
    else{
       return value
     }
  }
}



