import { Injectable } from '@angular/core';
@Injectable()

export class UserService{

  public account:string;
  public email:string;
  public gameTimes:number;
  public giftMoney:number;
  public integel:number;
  public loginToken:string = null;
  public nickName:string;
  public qq:string;
  public sex:number;
  public signerStatus:number;
  public uid:number;
  public wechat:string;
  public login:boolean = false;
  constructor(){

  }

  public setUserInfo(user){
    this.login = true;
    this.account = user.accout;
    this.email = user.email;
    this.gameTimes = user.gameTimes;
    this.giftMoney = user.giftMoney;
    this.integel = user.integel;
    this.loginToken = user.loginToken;
    this.nickName = user.nickName;
    this.qq = user.qq;
    this.sex = user.sex;
    this.signerStatus = user.signerStatus;
    this.uid = user.uid;
    this.wechat = user.wechat;
  }


}



