import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { HttpService } from '../../service/httpService';
import { NativeService } from '../../service/NativeService';
import { ArticlesDetailsPage } from '../articles-details/articles-details';



@Component({
  selector: 'page-newslist',
  templateUrl: 'newslist.html',
})
export class NewslistPage {

  private hotNews:any;
  private skills:any;
  private pageOne:any = 0;
  private pageTwo:any = 0;
  private showType = 'news';

  constructor(public navCtrl: NavController,
              public http:HttpService,
              public nat:NativeService,
              public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NewslistPage');
    this.getData('e')
    this.getSkills('e')
  }
  gotoArticle(id,type){
    console.log(id)
    this.navCtrl.push( ArticlesDetailsPage ,{id:id,type:type})
  }
  getData(e){
    let self = this
    let url =  ['otherWay','/openLottery/newsTitle']
    let options={page:self.pageOne,size:6}
    this.http.post(url,options,function (res,err) {
      if(!err){
        if(res.length == 0){
          self.pageOne = 'complete'
        }
        for(var i=0;i<res.length;i++){
          res[i].newsIntroduce = res[i].newsIntroduce.replace('&nbsp;','')
        }
        if(self.pageOne == 0){
          self.hotNews = res
        }else{
          self.hotNews = self.hotNews.concat(res)
          e.complete()
        }
      }
    })
  }

  getSkills(e){
    let self = this
    let url =  ['otherWay','/openLottery/skillTitle']
    let options={page:self.pageTwo,size:6}
    this.http.post(url,options,function (res,err) {
      if(!err){
        if(res.length == 0){
          self.pageTwo = 'complete'
        }
        for(var i=0;i<res.length;i++){
          res[i].newsIntroduce = res[i].newsIntroduce.replace('&nbsp;','')
        }
        if(self.pageTwo == 0){
          self.skills = res
        }else{
          self.skills = self.skills.concat(res)
          e.complete()
        }
      }
    })
  }
  changeShow(type){
    this.showType = type
  }
  doInfinite(e){
    if(this.showType == 'news'){
      if(this.pageOne == 'complete') {
        e.complete()
        return;
      }
      this.pageOne++
      this.getData(e)
    }else{
      if(this.pageTwo == 'complete') {
        e.complete()
        return;
      }
      this.pageTwo++
      this.getSkills(e)
    }
  }
}



