import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { LoginPage } from '../../account/login/login';
import { UserService } from '../../../service/userService';
import { ModifyPasswordPage } from '../modify-password/modify-password';
import {NativeService} from "../../../service/NativeService";
@Component({
    selector: 'page-setting',
    templateUrl: 'setting.html',
})
export class SettingPage {
    private dataSource: any;
    constructor(public navCtrl: NavController,
                public nat:NativeService,
                public user: UserService,
                public navParams: NavParams) {
        this.initData();
    }
    initData() {
        this.dataSource = [
            [
                {
                    icon: '../../assets/account/modified-icon.png',
                    name: '修改密码'
                }
            ]
        ];
    }
    clickAction(i, j) {
        if (i == 0) {
            if (j == 0) {
                if (!this.user.login) {
                    this.navCtrl.push(LoginPage);
                } else {
                 this.navCtrl.push(ModifyPasswordPage);
                }
            }
        }
    }
    ionViewDidLoad() {
        console.log('ionViewDidLoad SettingPage');
    }

    loginOutAction(){
        this.user.login = false;
        this.nat.storageRemoveKey("userInfo");
        this.navCtrl.pop();
    }

}



