import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import {HttpService} from "../../service/httpService";
import {UserService} from "../../service/userService";
import {LoginPage} from "../account/login/login";
import {ArticlesDetailsPage} from "../articles-details/articles-details";
import {NativeService} from "../../service/NativeService";


@Component({
  selector: 'page-plan-details',
  templateUrl: 'plan-details.html',
})
export class PlanDetailsPage {

  private plan:any;
  private expert = {
    nextForcastNumber:[],
  };
  private articlesList = [];
  private showLoading = true;
  constructor(public navCtrl: NavController,
              public http:HttpService,
              public user:UserService,
              public nat:NativeService,
              public navParams: NavParams) {

    this.plan = navParams.get("data");
    console.log(this.plan);
    this.plan.expertIntroduce = this.nat.delHtmlTag(this.plan.expertIntroduce);
    let url = "/api/expertDetails/getExpertDetailsByCode";
    let self = this;
    let map = {expertCode:this.plan.expertCode};
    this.http.post(url,map,function (res,err) {
      self.showLoading = false;
      if(!err){
        res.nextForcastNumber = res.nextForcastNumber.split(',');
        self.expert = res;
        console.log(res);
      }
    });

    url = "/api/forecastArticles/getArticlesNew";
    this.http.post(url,map,function (res,err) {

      if(!err){
        for(var i=0;i<res.length;i++){
          res[i].createTime = res[i].createTime.replace(/-/g,'/');
        }
        self.articlesList = res;
      }
    });

  }

  detailsAction(index){
    this.navCtrl.push(ArticlesDetailsPage,{id:this.articlesList[index].id});
  }

  praiseAction(){
    if(!this.user.login){
      this.navCtrl.push(LoginPage);
      return;
    }
    let url = "/api/expertDetails/updatePraiseNum";
    let self = this;
    this.http.post(url,{id:this.plan.id},function (res,err) {
      if(!err){
        self.plan.numberOfPraise += 1;
      }
    });
  }


}



