import {Component, Input, Output} from '@angular/core';
import { ActionSheetController} from 'ionic-angular';
import { HttpService }            from '../../service/httpService';

@Component({
  selector: 'pk10',
  templateUrl: 'pk10.html'
})
export class Pk10Component {

  private lotteryCode:string;
  private indexBarStr:string;      //当前第几球
  private indexBar:number = 0;     //第几球索引
  private navBar = [];             //时时彩导航
  private navBarIndex = 0;         //时时彩导航索引
  private number = 30;
  private numberStr = "近30期";
  private numbers = [];
  private types = [];
  private currentNum:string;

  @Input()
  set setLotteryCode(code:string){
    this.lotteryCode = code;
    this.getData();
  }
  constructor(public actionSheet:ActionSheetController,
              public http:HttpService) {
    this.indexBarStr = "冠军";
    this.navBar = [
      {
        select:true,
        type:0,
        name:'号码分布',
        item:[],
        url:'/api/lotteryOpenNumber/getNewestOpenNumberByLotteryCategory',
        dataSource:[],
        currentData:[]
      },
      {
        select:false,
        type:0,
        name:'号码走势',
        item:[],
        url:'/api/lotteryOpenNumber/getLotteryBaseTrend',
        dataSource:[],
        currentData:[]
      },{
        select:false,
        type:1,
        name:'冠亚和走势',
        subIndex:0,
        item:[],
        url:'/api/lotteryOpenNumber/getLotterySumTrend',
        dataSource:[],
        currentData:[]
      },{
        select:false,
        type:2,
        name:'位置走势',
        subIndex:0,
        item:[{
          select:true,
          subType:0,
          name:'号码分布',
        }, {
          select:false,
          subType:0,
          name:'形态特征',
        }, {
          select:false,
          subType:0,
          name:'012路',
        }, {
          select:false,
          subType:0,
          name:'升平降',
        }],
        url:'/api/lotteryOpenNumber/getLotteryPositionTrend',
        dataSource:[],
        currentData:[]
      },{
        select:false,
        type:3,
        name:'龙虎统计',
        item:[{
          select:true,
          subType:0,
          name:'奇偶个数',
        }, {
          select:false,
          subType:0,
          name:'奇偶比',
        }],
        url:'/api/lotteryOpenNumber/getDragonTigerOfPk',
        dataSource:[],
        currnetData:[]
      },{
        select:false,
        type:4,
        name:'冷热分析',
        item:[{
          select:true,
          subType:0,
          name:'和值走势',
        }, {
          select:false,
          subType:0,
          name:'和值形态',
        }],
        url:'/api/lotteryOpenNumber/getColdAndHotAnalysis',
        dataSource:[],
        currentData:[]
      }
    ];
    this.numbers = [
      {select:false,num:'01',numInt:1},
      {select:false,num:'02',numInt:2},
      {select:false,num:'03',numInt:3},
      {select:false,num:'04',numInt:4},
      {select:false,num:'05',numInt:5},
      {select:false,num:'06',numInt:6},
      {select:false,num:'07',numInt:7},
      {select:false,num:'08',numInt:8},
      {select:false,num:'09',numInt:9},
      {select:false,num:'10',numInt:10},
    ];
    this.types = [
      {select:false,num:'单'},
      {select:false,num:'双'},
      {select:false,num:'大'},
      {select:false,num:'小'},
    ];
  }
  ngAfterViewChecked(){
    if(this.navBarIndex == 1){
      let svg = document.getElementById('svg');
      let node = document.getElementsByClassName('zhong');
      svg.innerHTML = this.createLine(node,'#00baff');
    }else if(this.navBarIndex == 2){
      let svg = document.getElementById('svg1');
      let node = document.getElementsByClassName('zhong1');
      svg.innerHTML = this.createLine(node,'#00baff');
    }else if(this.navBarIndex == 3 && this.navBar[3].subIndex == 0){
      let svg = document.getElementById('svg2');
      let node = document.getElementsByClassName('zhong2');
      svg.innerHTML = this.createLine(node,'#00baff');
    }

  }

  createLine(bars,lineColor){
    let svgStr = "";
    if(bars.length <= 0)return;
    for(var i=0;i<this.number - 1;i++){
      let td = bars[i].parentNode;
      let top = td.offsetTop;
      let left = td.offsetLeft;
      let width = td.offsetWidth / 2;
      let height = td.offsetHeight / 2;
      let tdX = left + width;
      let tdY = top + height;
      let td1 = bars[i+1].parentNode;
      let top1 = td1.offsetTop;
      let left1 = td1.offsetLeft;
      let width1 = td1.offsetWidth / 2;
      let height1 = td1.offsetHeight / 2;
      let td1X = left1 + width1;
      let td1Y = top1 + height1;
      svgStr += "<line style='stroke:"+ lineColor + "' x1='"+tdX+"' y1='"+tdY+"' x2='"+td1X+"' y2='"+td1Y+"'/>";
    }
    return svgStr;
  }

  getData(){
    let self = this;
    let url = this.navBar[this.navBarIndex].url;
    let navBarIndex = self.navBarIndex//防止快速切换navBarIndex 数据请求前和请求完毕后navBarIndex不同而报错
    
    this.http.post(url,{lotteryCategoryCode:this.lotteryCode,number:this.number},function (data,err) {
      if(!err){
        if(navBarIndex == 1 || navBarIndex == 3){
          for(var i = 0;i<data.length;i++){
            data[i].trendList = data[i].trendList.reverse();
            for(var j=0;j<data[i].trendList.length;j++){
              let item = data[i].trendList[j];
              item.openNumber = item.openNumber.split(',');
            }
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data[self.indexBar].trendList;
        }else if(navBarIndex == 2){
          //console.log(data)
          data.trendList = data.trendList.reverse();
          for(var j=0;j<data.trendList.length;j++){
            let item = data.trendList[j];
            item.openNumber = item.openNumber.split(',');
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data.trendList;
        }else if(navBarIndex == 4){
          for(var j=0;j<data.length;j++){
            let item = data[j];
            item.createTime = item.createTime.substring(2,10);
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data;
        }else if(navBarIndex == 5){

          for(var i=0;i<data.length;i++) {
            let group = data[i];
            if (group.rank == 1) {
              group.ranking = "冠军";
            } else if (group.rank == 2) {
              group.ranking = "亚军";
            } else if (group.rank == 3) {
              group.ranking = "季军";
            } else if (group.rank == 4) {
              group.ranking = "第四名";
            } else if (group.rank == 5) {
              group.ranking = "第五名";
            }else if (group.rank == 6) {
              group.ranking = "第六名";
            }else if (group.rank == 7) {
              group.ranking = "第七名";
            }else if (group.rank == 8) {
              group.ranking = "第八名";
            }else if (group.rank == 9) {
              group.ranking = "第九名";
            }else if (group.rank == 10) {
              group.ranking = "第十名";
            }
            for (var j = 0; j < group.list.length; j++) {
              let item = group.list[j];
              if (item.state == 1) {
                item.name = "热号";
              } else if (item.state == 2) {
                item.name = "温号";
              } else if (item.state == 3) {
                item.name = "冷号";
              }
            }
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data;
        }else{
          for(var i=0;i<data.length;i++) {
            let array = data[i].lotteryOpenNumber.split(',');
            data[i].openNumber = [];
            for(var j=0;j<array.length;j++){
              data[i].openNumber.push({num:parseInt(array[j]),opacity:false});
            }
          }
          self.navBar[navBarIndex].dataSource = data;
          self.navBar[navBarIndex].currentData = data;
        }
      }
    })
  }

  onSelectItem(index){
    if(this.navBarIndex == index) return;
    this.navBarIndex = index;
    this.indexBar = 0;
    this.indexBarStr = "第一球";
    this.getData();
  }
  selectSubBarAction(index){
    for(var i=0;i<this.navBar[this.navBarIndex].item.length;i++){
      this.navBar[this.navBarIndex].item[i].select = false;
    }
    this.navBar[this.navBarIndex].item[index].select = true;
    this.navBar[this.navBarIndex].subIndex = index;
  }

  //当前第几球
  selectBarIndex(){
    let actionSheet = this.actionSheet.create({
      buttons: [
        {text: '冠军', handler: () => {this.setCurrentIndexBar(0,'冠军');}},
        {text: '亚军', handler: () => {this.setCurrentIndexBar(1,'亚军');}},
        {text: '第三名', handler: () => {this.setCurrentIndexBar(2,'第三名');}},
        {text: '第四名', handler: () => {this.setCurrentIndexBar(3,'第四名');}},
        {text: '第五名', handler: () => {this.setCurrentIndexBar(4,'第五名');}},
        {text: '第六名', handler: () => {this.setCurrentIndexBar(5,'第六名');}},
        {text: '第七名', handler: () => {this.setCurrentIndexBar(6,'第七名');}},
        {text: '第八名', handler: () => {this.setCurrentIndexBar(7,'第八名');}},
        {text: '第九名', handler: () => {this.setCurrentIndexBar(8,'第九名');}},
        {text: '第十名', handler: () => {this.setCurrentIndexBar(9,'第十名');}},
        {text: '取消', role: 'cancel', handler: () => {console.log('Cancel clicked');}}
      ]
    });
    actionSheet.present();
  }


  setCurrentIndexBar(index,indexName){
    this.indexBarStr = indexName;
    this.indexBar = index;
    this.navBar[this.navBarIndex].currentData = this.navBar[this.navBarIndex].dataSource[index].trendList;
  }

  selectNumberIndexAction(){

    let actionSheet = this.actionSheet.create({
      buttons: [
        {
          text: '近30期',
          handler: () => {
            this.numberStr = "近30期";
            this.number = 30;
            this.getData();
          }
        },
        {
          text: '近60期',
          handler: () => {
            this.numberStr = "近60期";
            this.number = 60;
            this.getData();
          }
        },
        {
          text: '近90期',
          handler: () => {
            this.numberStr = "近90期";
            this.number = 90;
            this.getData();
          }
        },
        {
          text: '取消',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  selectTypeAction(index){

    for(var i=0;i<this.numbers.length;i++){
      this.numbers[i].select = false;
    }
    for(var i=0;i<this.types.length;i++){
      this.types[i].select = false;
    }
    this.types[index].select = true;

    let data = this.navBar[this.navBarIndex].currentData;
    for(var i=0;i<data.length;i++){
      for(var j=0;j<data[i].openNumber.length;j++){
        if(index == 0){
          if(data[i].openNumber[j].num % 2 != 0){
            data[i].openNumber[j].opacity = false;
          }else {
            data[i].openNumber[j].opacity = true;
          }
        }else if(index == 1){
          if(data[i].openNumber[j].num % 2 == 0){
            data[i].openNumber[j].opacity = false;
          }else {
            data[i].openNumber[j].opacity = true;
          }
        }else if(index == 2){
          if(data[i].openNumber[j].num >= 6){
            data[i].openNumber[j].opacity = false;
          }else {
            data[i].openNumber[j].opacity = true;
          }
        }else if(index == 3){
          if(data[i].openNumber[j].num <= 5){
            data[i].openNumber[j].opacity = false;
          }else {
            data[i].openNumber[j].opacity = true;
          }
        }
      }
    }
  }

  selectNumberAction(index){

    for(var i=0;i<this.types.length;i++){
      this.types[i].select = false;
    }
    for(var i=0;i<this.numbers.length;i++){
      this.numbers[i].select = false;
    }
    this.numbers[index].select = true;

    let data = this.navBar[this.navBarIndex].currentData;
    for(var i=0;i<data.length;i++){
      for(var j=0;j<data[i].openNumber.length;j++){
        if(this.numbers[index].numInt == data[i].openNumber[j].num){
          data[i].openNumber[j].opacity = false;
        }else {
          data[i].openNumber[j].opacity = true;
        }
      }
    }
  }
}




