import { Component } from '@angular/core';
import { NavController, NavParams ,ModalController} from 'ionic-angular';
import {LotterysPage} from "../lotterys/lotterys";
import {HttpService} from "../../service/httpService";

@Component({
  selector: 'page-statistic',
  templateUrl: 'statistic.html',
})
export class StatisticPage {

  private currentLottery = {
    'title':'重庆时时彩',
    'code':'cqssc',
    'type':'ssc'
  };

  private lotterys :any;

  constructor(public navCtrl: NavController,
              public modal:ModalController,
              public http:HttpService,
              public navParams: NavParams) {
    this.getAllLotterys();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad StatisticPage');
  }

  getAllLotterys(){

    let url = "/api/lotteryCategory/getTrendLotteryCategoryList";
    let self = this;
    this.http.post(url,{},function (res,err) {
      if(!err){
        for(var i=0;i<res.length;i++){
          let group = res[i];
          for (var j=0;j<group.list.length;j++){
            let item = group.list[j];
            item.select = false;
            if(i == 0 && j == 0){
              item.select = true;
            }
          }
        }
        self.lotterys = res;
      }
    })
  }

  selectLotteryAction(){

    let modal = this.modal.create(LotterysPage,{lotterys:this.lotterys,callback:this.callback});
    modal.present();
  }

  callback = (data)=>{
    return new Promise((res,rej)=>{
      this.currentLottery = data;
    });
  };
}



