import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule }          from '@angular/platform-browser';
import { BrowserAnimationsModule }from "@angular/platform-browser/animations";
import { HttpClientModule }       from '@angular/common/http';
import { IonicApp, IonicModule }  from 'ionic-angular';
import { IonicErrorHandler }      from 'ionic-angular';
import { MyApp }                  from './app.component';

import { Clipboard }              from '@ionic-native/clipboard';
import { ActionSheet }            from '@ionic-native/action-sheet';
import { HTTP }                   from '@ionic-native/http';
import { Badge }                  from '@ionic-native/badge';
import { Camera }                 from '@ionic-native/camera';
import { DatePicker }             from '@ionic-native/date-picker';
import { Device }                 from '@ionic-native/device';
import { Dialogs }                from '@ionic-native/dialogs';
import { File }                   from '@ionic-native/file';
import { Keyboard }               from '@ionic-native/keyboard';
import { SpinnerDialog }          from '@ionic-native/spinner-dialog';
import { NativeStorage }          from '@ionic-native/native-storage';
import { SplashScreen }           from '@ionic-native/splash-screen';
import { StatusBar }              from '@ionic-native/status-bar';
import { Toast }                  from '@ionic-native/toast';
import { BrowserTab }             from '@ionic-native/browser-tab';

import { NgxEchartsModule }       from 'ngx-echarts';
import { MultiPickerModule }      from 'ion-multi-picker';
import { HttpService }            from '../service/httpService';
import { NativeService }          from "../service/NativeService";
import { UserService }            from "../service/userService";

import { ComponentsModule }       from '../components/components.module';
import { PipesModule }            from '../pipes/pipes.module';

import { HomePage }               from '../pages/home/home';
import { HistoryPage }            from '../pages/home/lottery/history';
import { PlanPage }               from '../pages/home/plan/plan';
import { ExpertPage }             from '../pages/home/expert/expert';
import { LotteryPage }            from '../pages/home/lottery/lottery';
import { NewsPage }               from '../pages/home/news/news';
import { NewsDetailsPage }        from '../pages/home/news-details/news-details';

import { HallPage }               from '../pages/hall/hall';

import { HistoryListPage }            from '../pages/historylist/historylist';


import { NewslistPage }           from '../pages/newslist/newslist';

import { StatisticPage }          from '../pages/statistic/statistic';
import { PredictionPage }         from '../pages/prediction/prediction';

import { LotterysPage }           from '../pages/lotterys/lotterys';
import { PlanDetailsPage }        from '../pages/plan-details/plan-details';
import { ArticlesDetailsPage }    from '../pages/articles-details/articles-details';

import { ForecastPage }           from '../pages/forecast/forecast';
import { DetailsPage }            from '../pages/forecast/details/details';

import { MePage }                 from '../pages/me/me';
import { FindPage }               from '../pages/me/find/find';
import { CollectionPage }         from '../pages/me/collection/collection';
import { SettingPage }            from '../pages/me/setting/setting';
import { UserInfoPage }           from '../pages/me/userInfo/userInfo';
import { AboutPage }              from '../pages/me/about/about';
import { ModifyPasswordPage }     from '../pages/me/modify-password/modify-password';
import { FindPasswordPage }       from '../pages/account/find-password/find-password';
import { LoginPage }              from '../pages/account/login/login';
import { RegisterPage }           from '../pages/account/register/register';

import { TrendPage }              from '../pages/trend/trend';

import { TabsPage }               from '../pages/tabs/tabs';

@NgModule({
  declarations: [
    MyApp,                  //主入口
    HomePage,               //首页
    ForecastPage,           //预测计划
    TrendPage,              //走势
    MePage,                 //我的
    AboutPage,              //关于我们
    TabsPage,               // tabBarPages
    LotteryPage,            // 开奖页面
    LotterysPage,           // 采种页面
    UserInfoPage,           // 用户信息
    SettingPage,            //设置页面
    ModifyPasswordPage,     //设置密码
    FindPage,               //发现兴趣
    CollectionPage,         // 我的收藏
    PlanPage,               // 专家计划
    ExpertPage,             // 专家详情
    FindPasswordPage,       // 找回密码
    LoginPage,              //登入密码
    DetailsPage,            // 计划详情
    RegisterPage,           // 注册账号
    NewsDetailsPage,        //新闻详情
    NewsPage,               //新闻页面
    StatisticPage,          //统计页面
    PredictionPage,         // 预测计划
    PlanDetailsPage,        // 专家详情
    ArticlesDetailsPage,    // 预测详情
    HistoryListPage,            // 开奖历史,
    HallPage,                //彩票大厅
    NewslistPage,            //新闻列表
    HistoryPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    ComponentsModule,
    PipesModule,
    MultiPickerModule,
    NgxEchartsModule,
    BrowserAnimationsModule,
    IonicModule.forRoot(MyApp, {
      mode: 'ios',//android是'md'
      backButtonText: '返回',
      tabsHideOnSubPages: 'true'
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ForecastPage,
    TrendPage,
    MePage,
    AboutPage,
    TabsPage,
    LotteryPage,
    LotterysPage,
    UserInfoPage,
    SettingPage,
    ModifyPasswordPage,
    FindPage,
    CollectionPage,
    PlanPage,
    ExpertPage,
    FindPasswordPage,
    LoginPage,
    DetailsPage,
    RegisterPage,
    NewsDetailsPage,
    NewsPage,
    StatisticPage,
    PredictionPage,
    PlanDetailsPage,
    ArticlesDetailsPage,
    HistoryListPage,
    HallPage,
    NewslistPage,
    HistoryPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    HTTP,Toast,NativeStorage,ActionSheet,SpinnerDialog,BrowserTab,
    Clipboard,Badge,Camera,DatePicker,Device,Dialogs,File,Keyboard,
    HttpClientModule,
    HttpService,
    NativeService,
    UserService,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



