import { Component } from '@angular/core';
import { NavController, NavParams, Platform } from 'ionic-angular';
import { UserInfoPage }             from './userInfo/userInfo';
import { FindPage }                 from './find/find';
import { CollectionPage }           from './collection/collection';
import { SettingPage }              from './setting/setting';
import { LoginPage }                from '../account/login/login';
import { UserService } from '../../service/userService';
import {HttpService} from "../../service/httpService";
import {Clipboard} from "@ionic-native/clipboard";
import {NativeService} from "../../service/NativeService";


@Component({
  selector: 'page-me',
  templateUrl: 'me.html',
})
export class MePage {

  private dataSource:any;
  private weixin:string = '';
  private showLoading = true;
  constructor(public navCtrl: NavController,
              public user:UserService,
              public clip:Clipboard,
              private http:HttpService,
              public nat:NativeService,
              public navParams: NavParams) {
    this.initData();
  }


  initData(){

    this.dataSource = [  
      {
        icon:'./assets/account/user-icon.png',
        name:'个人信息'
      },{
        icon:'./assets/account/share-icon.png',
        name:'分享应用'
      },{
        icon:'./assets/account/find-icon.png',
        name:'发现兴趣'
      },{
        icon:'./assets/account/like-icon.png',
        name:'我的收藏'
      },{
        icon:'./assets/account/set-icon.png',
        name:'系统设置'
      }
    ];
    let self = this;
    let url = "/api/servicenumber/getWeixinhao";
    this.http.post(url,{},function (res,err) {
      self.showLoading = false;
      if(!err){
        console.log(res);
        self.weixin = res.content;
      }
    });


  }

  loginAction(){
    if(!this.user.login){
      this.navCtrl.push(LoginPage);
    }
  }

  clickAction(i){
    switch(i){
      case 0:
        console.log(0);
        if(!this.user.login){
          this.navCtrl.push(LoginPage);
        }else {
          this.navCtrl.push(UserInfoPage);
        }
        break;
      case 1:
        console.log(1);
        break;
      case 2:
        console.log(2);
        this.navCtrl.push(FindPage);
        break;
      case 3:
        console.log(3);
        if(!this.user.login){
          this.navCtrl.push(LoginPage);
          return;
        }else{
          this.navCtrl.push(CollectionPage);
        }
        break;
      case 4:
        console.log(4)
        if(!this.user.login){
          this.navCtrl.push(LoginPage);
          return;
        }else{
          this.navCtrl.push(SettingPage);
        }
        break;
    }   
  }

  copyAction(){
    if (this.weixin && this.weixin.length > 0){
      if(this.nat.isMobile()){
        this.clip.copy(this.weixin).then( res=>{
          this.nat.showToast("复制成功");
        });
      }else{
        console.log("这不是手机环境 别复制了")
      }     
    }
  }
}



