import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HttpService } from '../../../service/httpService';
import { NativeService } from '../../../service/NativeService';


@Component({
    selector: 'page-history',
    templateUrl: 'history.html',
})
export class HistoryPage {

    private showLoading=true;
    private data:any;
    private lottery:any;
    constructor(public navCtrl: NavController,
                public http:HttpService,
                public nat:NativeService,
                public navParams: NavParams) {
        this.lottery = navParams.get('data');
        console.log(this.lottery);
        this.getData();
    }



    getData(){


        let url = "/api/lotteryOpenNumber/getNewestOpenNumberByLotteryCategory";
        let map = {lotteryCategoryCode:this.lottery.lotteryCategoryCode,number:100};
        let self = this;
        this.http.post(url,map,function (res,err) {
            self.showLoading = false;
            if(!err){
                for(var i=0;i<res.length;i++){
                    res[i].numbers = res[i].lotteryOpenNumber.split(',');
                    for(var j=0;j<res[i].numbers.length;j++){
                        res[i].numbers[j] = parseInt(res[i].numbers[j]);
                    }
                }
                self.data = res;
                console.log(res);
            }
        });
    }
}



