import { Component } from '@angular/core';
import { NavController,AlertController } from 'ionic-angular';
import { UserService } from '../../../service/userService';
import {HttpService} from "../../../service/httpService";

@Component({
  selector: 'page-userInfo',
  templateUrl: 'userInfo.html'
})
export class UserInfoPage {

  constructor(public navCtrl: NavController,
              public alertCtrl:AlertController,
              public http:HttpService,
              public user:UserService) {



  }

  selectSex(){

    let alert = this.alertCtrl.create();
    alert.setTitle('选择性别');

    alert.addInput({
      type: 'radio',
      label: '男',
      value: '1',
      checked: (this.user.sex == 1 || this.user.sex == 2) ? true : false
    });
    alert.addInput({
      type: 'radio',
      label: '女',
      value: '0', 
      checked: this.user.sex == 0 ? true : false
    });
    alert.addButton('取消');
    alert.addButton({
      text: '确定',
      handler: data => {
        this.user.sex = data;
        this.changeUserInfo();
      }
    });
    alert.present();
  }


  settingEmail(){
    const prompt = this.alertCtrl.create({
      title: '邮箱设置',
      inputs: [
        {
          name: 'email',
          type:'email',
          placeholder: '请输入邮箱地址'
        },
      ],
      buttons: [{
          text: '取消',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: '确定',
          handler: data => {
            console.log(data);
            this.user.email = data.email;
            this.changeUserInfo();
          }
        }
      ]
    });
    prompt.present();
  }

  compareFn(a,b){
    var key=null
    if(!key){
      console.log(a)
      key='1'
    }
  }
  // settingNi(){
  //   const prompt = this.alertCtrl.create({
  //     title: '昵称设置',
  //     inputs: [
  //       {
  //         name: 'nicheng',
  //         type:'text',
  //         placeholder: '请输入昵称'
  //       },
  //     ],
  //     buttons: [{
  //         text: '取消',
  //         handler: data => {
  //           console.log('Cancel clicked');
  //         }
  //       },
  //       {
  //         text: '确定',
  //         handler: data => {
  //           console.log(data);
  //           this.user.email = data.email;
  //           this.changeUserInfo();
  //         }
  //       }
  //     ]
  //   });
  //   prompt.present();
  // }

  changeUserInfo(){

    let url = ['otherWay','/api/userinfo//modifyUserinfo'];

    let map = {
      uid:this.user.uid,
      loginToken:this.user.loginToken,
      email:this.user.email,
      sex:this.user.sex
    };

    this.http.post(url,map,function (res,err) {

      if(!err){
        console.log(res);
      }
    });
  }




}



