import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'custompipe',})
export class CustompipePipe implements PipeTransform {
  /**
   * Takes a value and makes it lowercase.
   */
  transform(value: string, ...args) {
    let number = parseInt(value);
    if(args[0] == '') return number < 0 ? (number * -1) : number;
    if(number >= 0){
      if(args[0] == 'even') return "偶";
      if(args[0] == 'reliance') return "奇";
      if(args[0] == 'big') return "大";
      if(args[0] == 'small') return "小";
      if(args[0] == 'with') return "和";
      if(args[0] == 'quality') return "质";
      if(args[0] == '0') return "0";
      if(args[0] == '1') return "1";
      if(args[0] == '2') return "2";
      if(args[0] == '3:0') return "3:0";
      if(args[0] == '2:1') return "2:1";
      if(args[0] == '1:2') return "1:2";
      if(args[0] == '0:3') return "0:3";
      if(args[0] == 'shen') return "升";
      if(args[0] == 'pin') return "平";
      if(args[0] == 'bao') return "豹";
      if(args[0] == 'sun') return "顺";
      if(args[0] == 'dui') return "对";
      if(args[0] == 'ban') return "半";
      if(args[0] == 'za') return "杂";
      if(args[0] == 'zhus') return "三";
      if(args[0] == 'zhul') return "六";
      if(args[0] == '龙') return "龙";
      if(args[0] == '虎') return "虎";
      if(args[0] == '和') return "和";
      if(args[0] == '单') return "单";
      if(args[0] == '双') return "双";
      if(args[0] == 'baozi') return "豹子";
      if(args[0] == 'sbt') return "三不同";
      if(args[0] == 'duizi') return "对子";
    }
    return number * -1;
  }




  

}



